* {
	font-family: 'Roboto', serif; }

header {
	position: relative; }

nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background-color: #212121;
	z-index: 2; }

.menu {
	display: flex;
	justify-content: space-between;
	margin: 0;
	padding: 13px 70px;
	list-style-type: none;

	&_item {
		border-right: 2px dotted #000000;
		padding-right: 28px;

		&:last-child {
			border-right: none; }

		&_link {
			color: #fff;
			font-size: 15px;
			font-weight: 300; } } }

.subheader {
	top: 50px;
	height: 84px;
	background-color: rgba(0, 0, 0, 0.6);
	position: absolute;
	width: 100%;

	&_logo {
		display: block;
		max-width: 100%;
		margin-top: 16px; }

	&_text {
		margin-top: 6px;
		color: #ffffff;
		font-size: 13px;
		font-weight: 400;
		opacity: 0.5; }

	&_call {
		margin-top: 19px;
		color: #ffffff;
		font-size: 12px;
		font-weight: 300;
		text-align: right; }

	&_phone {
		display: block;
		color: #ffffff;
		font-size: 22px;
		font-weight: 400;
		text-transform: uppercase;
		text-align: right;
		line-height: 26px; }

	&_btn {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 3px;
		background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 14px;
		font-weight: 400;
		text-transform: uppercase; } }

.promo {
	min-height: 800px;
	background: url('../img/main_bg.jpg') center (center / cover) no-repeat;
	text-align: center;
	padding: 150px 0 135px;

	&_header {
		margin: 0;
		color: #ffffff;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 60px;
		line-height: 60px; }

	&_subheader {
		margin: 0;
		margin-top: 22px;
		color: #ffffff;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 36px;
		line-height: 36px; }

	&_descr {
		margin-top: 23px;
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px; }

	&_btn {
		width: 259px;
		height: 63px;
		box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 3px;
		background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase;
		margin-top: 118px; } }

.reasons {
	min-width: 900px;
	padding: 70px 0 65px;

	.reasons_adv {
		margin: 0 auto;
		width: 112px;
		height: 24px;
		background-color: #000000;
		color: #ffffff;
		font-size: 11px;
		font-weight: 400;
		text-transform: uppercase;
		text-align: center;
		line-height: 25px; }

	.reasons_header {
		color: #222222;
		font-family: Roboto;
		font-size: 38px;
		font-weight: 700;
		line-height: 28px;
		text-transform: uppercase;
		margin: 38px auto 0;
		text-align: center; }

	.reasons_item {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 40px;

		.reasons_round {
			display: flex;
			justify-content: center;
			align-items: center;
			margin-right: 26px;
			width: 113px;
			height: 113px;
			background-color: #1eacc7;
			border-radius: 100%; }

		.reasons_text {
			width: 343px;

			.reasons_title {
				color: #1eacc7;
				font-size: 17px;
				font-weight: 400; }

			.reasons_descr {
				color: #202020;
				font-size: 15px;
				font-weight: 300;
				line-height: 21px;
				text-align: justify; } } } }
